<template>
      <el-menu router :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" >
        <el-submenu style="text-align: right" v-for="(item,index) in $router.options.routes"
                    v-if="item.hasOwnProperty('children')&&item.children.length>0&&item.show"
                    :key = "index"
                    :index = "index+''"
                    :popper-append-to-body="true">
          <template #title>
            <span slot="title">{{item.name}}</span>
          </template>
          <el-menu-item v-for="(item2,index2) in item.children"
                        v-if="item2.show" :popper-append-to-body="true"
                        :key = "index2"
                        :index="item2.path"
                        :class="$route.path==item2.path?'is-active':''" >{{item2.name}}
          </el-menu-item>
        </el-submenu>
        <el-menu-item v-else-if="item.show" :popper-append-to-body="true"
                      :index="item.path"><router-link to="item.path"></router-link>
          <span>{{item.name}}</span>
        </el-menu-item>
      </el-menu>
</template>

<script>
export default {
  name: "Header"
}
</script>

