<template>
  <el-container>

    <el-header>
      <my-header class='m-header'></my-header>
    </el-header>
    <el-main>
      <div :style="note">
        <div class="position">{{furigana}}</div>
        <div class="position2">{{name}}</div>
        <div >
          <div class="block">
            <el-switch
                style="display: block "
                v-model="isSwitch"
                active-color="#13ce66"
                inactive-color="#ff4949"
                inactive-text="ノーマル"
                active-text="拡大"
                :disabled="isDisable"
                @change="changeSwitch($event)">
            </el-switch>
          </div>
          <div class="block2">
            <el-slider
                id="range"
                v-model="sliderNumber"
                ref="slider"
                :step="1"
                :min="1"
                :max="zoomCount"
                :hidden="hidden"
                show-stops
                @change="changeSlider($event)"
            >
            </el-slider></div>
        </div>
        <div  class="image-middle"  v-cloak>
          <canvas   id="image" width="1024px" height="1024px"  :style="{'--height': sizeVar,'--width': sizeVar}"
                    v-on:touchstart.prevent="imgTouchStart"
                    v-on:touchmove.prevent="imgTouchMove"
                    v-on:touchend.prevent="imgTouchEnd"
                    @mousedown="imgMouseStart($event)"
                    @mousemove.prevent="imgMouseMove($event)"
                    @mouseup="imgMouseEnd($event)"
                    @mouseleave.prevent="imgMouseEnd($event)"
                    @mousewheel.prevent="imgMouseWheel($event)"
          ></canvas>
        </div>
        <div class="elbutton">
          <el-button icon="el-icon-arrow-up" @click.native="turnUp"> </el-button>
          <el-button icon="el-icon-arrow-down" @click.native="turnDown"> </el-button>
          <el-button icon="el-icon-arrow-left" @click.native="turnLeft"> </el-button>
          <el-button icon="el-icon-arrow-right" @click.native="turnRight"> </el-button>
        </div>
        <div class="text">
          <el-divider></el-divider>
          <div>
            <span style="align-content: center">{{bottomText}}</span>
          </div>
          <el-divider></el-divider>
        </div>
      </div>

    </el-main>
  </el-container>

</template>

<script>


import header from '@/components/Header'

var imgSrc =[]

export default {
  components: {
    'my-header': header,
  },
  props(){
    imgSrc:{
      type:String
    }
    windowHeight:{
      type:Number
    }
    windowWidth:{
      type:Number
    }
  },
  name: "content",

  data() {
    return {
      note: {
        backgroundImage: "url(" + require("/public/background.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundClip: "content-box",
        width: "100%",
        height: "100%",
        position: "absolute",
        left: "0px",
      },
      bottomText:"著作権：弓箭閣　計測：山本周作　計測日：2019/8/11　作成者：山本周作・柴野将尚・張子安",
      sizeVar : "1024px",
      name:"",
      furigana: "",
      isSwitch: false,
      isDisable: false,
      sliderNumber: 1,
      hidden: true,
      countX:24, //回転画像内でX方向の画像数
      normalX:24,
      countY:24, //回転画像内でY方向の画像数
      normalY:24,
      enlargeCountX:24,//拡大画像内でX方向の画像数
      enlargeCountY:10,//Y方向の画像数
      zoomCount:4, //画像の距離（遠近）数（拡大レベル）
      zoomNow:1,  //今画像の拡大レベル
      x:0, //今　x方向　　第何枚
      y:0, //今　y方向　　第何枚
      addressName:'',//画像を保存されるパス（頭だけ）　　原因：Vue.jsの「require()」はパスの定義と制限があり，「require()」では、フルパスの動的割り当てはできず、一部分の動的割り当ては可能です。
      imgType : "webp",
      imgSrc:"",//画像を保存されるパス
      imgDataset:[[]],//画像群
      touchStartPointX:0,//タッチ操作を制御
      touchStartPointY:0,
      touchSumX:0,
      touchSumY:0,
      mouseFlag:0,//マウス操作を制御
      fingerFlag:0,//指の数＞１>>flag=1  >>拡大・縮小をモニターする
      fingerStart:[],
      fingerMove:[],
      oldZoom:1,
      wheelFlag:1,//マウスホイール操作を制御
      wheelFlag2:0,
      extraPara:[],//今後もし新たな鎧兜情報（紹介など）を導入すれば活用できる　　　　　
      extraData:[],
      extraParameter:[],//今後もし新たな鎧兜情報（紹介など）を導入すれば活用できる
      extraDataset:[],
      drawFlag:0,
    }
  },
  methods:{
    changeSwitch($event){
      const _this= this
      let bar = document.getElementById("range")
      if($event == true)
      {
        _this.hidden =false;

      }
      else
      {
        _this.hidden =true;
        _this.sliderNumber = 1;
        _this.changeSlider(_this.sliderNumber)
      }
    },
    changeSlider($event){
      const _this= this
      try{
        for(var i=1;i<=_this.zoomCount;i++){
          if($event == i)
          {
            _this.zoomNow = i
            if(i > 1) {
              _this.countY = _this.enlargeCountY
              _this.loadimg(_this.enlargeCountY, _this.enlargeCountX)
            }
            else{
              _this.countY = _this.normalY
              _this.loadimg(_this.normalY,_this.normalX)
            }
            if(_this.y > _this.countY-1)  _this.y = _this.countY-1
            _this.imgSrc = _this.imgDataset[_this.y][_this.x]
            _this.drawframe()
          }
        }
      }
      catch (e) {
      }
    },
    turnRight() {
      const _this = this
      if(_this.x < _this.countX-1){
        _this.x = _this.x + 1;
      }
      else
      {
        _this.x = 0;
      }
      this.imgSrc = _this.imgDataset[_this.y][_this.x]
      this.drawframe()
    },
    turnLeft() {
      const _this = this
      if(_this.x > 0) {
        _this.x = _this.x - 1;
      }
      else
      {
        _this.x = _this.countX-1;
      }
      this.imgSrc = _this.imgDataset[_this.y][_this.x]
      this.drawframe()
    },
    turnUp() {
      const _this = this
      if(_this.y < _this.countY-1){
        _this.y = _this.y + 1;
      }
      else
      {
        _this.y = 0;
      }
      this.imgSrc = _this.imgDataset[_this.y][_this.x]
      this.drawframe()
    },
    turnDown() {
      const _this = this
      if(_this.y > 0) {
        _this.y = _this.y - 1;
      }
      else
      {
        _this.y = _this.countY-1;
      }
      this.imgSrc = _this.imgDataset[_this.y][_this.x]
      this.drawframe()
    },
    loadimg(count1,count2){
      const _this = this
      this.imgDataset=[[]];
      var foldersrc = _this.addressName + '/' + _this.imgType +  '/' + _this.zoomNow
      try{
        for (var i = 0; i < count1; i++) {
          _this.imgDataset[i] = new Array()
          for (var j = 0; j < count2; j++) {
            var src = foldersrc + '/data' + i + '/' + 'image' + ('000' + j).slice(-3)
            _this.imgDataset[i][j] = require('/public/picdata/' + src + '.' + _this.imgType);
          }
        }
      }
      catch (e){
      }
    },
    drawframe () {
      // 画像がページでレンダリング
      const _this = this

      var can = document.getElementById('image')
      var cxt = can.getContext('2d')
      var img = new Image()
      img.src = this.imgSrc
      img.onload = function () {
        cxt.drawImage(img, 0, 0)
      }
    },
    sizeinitialize(){
      const _this = this
      _this.windowHeight = document.documentElement.clientHeight; // 高
      _this.windowWidth = document.documentElement.clientWidth; // 宽
      if(_this.windowHeight - 200 > _this.windowWidth){
        var imgsize = _this.windowWidth
      }
      else {
        var imgsize =_this.windowHeight - 200
      }
      if(imgsize < 1024){
        _this.sizeVar = imgsize + 'px'
      }
    },
    getDistance(p1, p2) {
      var x = p2.pageX - p1.pageX,
          y = p2.pageY - p1.pageY;
      return Math.sqrt((x * x) + (y * y));
    },
    imgTouchStart:function (event){
      const _this = this
      if (event.targetTouches.length === 1) {
        _this.touchStartPointX = event.targetTouches[0].pageX;
        _this.touchStartPointY = event.targetTouches[0].pageY;
      }
      else{
        _this.oldZoom = _this.zoomNow
        _this.fingerStart = event.targetTouches
        _this.fingerFlag = 1
      }
    },
    zoomChange(event){
      const _this = this
      _this.fingerMove = event.targetTouches
      var scale = (_this.getDistance(_this.fingerMove[0], _this.fingerMove[1]) - _this.getDistance(_this.fingerStart[0], _this.fingerStart[1]))
      var zoomChangeCount = parseInt((_this.oldZoom + scale / (_this.windowWidth * 0.1)))
      if(zoomChangeCount > 1){
        _this.isSwitch = true
        _this.changeSwitch(_this.isSwitch)
        if(zoomChangeCount > _this.zoomCount) zoomChangeCount = _this.zoomCount
      }
      else{
        _this.isSwitch = false
        _this.changeSwitch(_this.isSwitch)
        if(zoomChangeCount < 1) zoomChangeCount = 1
      }
      _this.changeSlider(zoomChangeCount)
      _this.sliderNumber = zoomChangeCount
    },
    imgTouchMove:function (event){
      const _this = this
      var pc_x = _this.countX;  // x方向で画像を切り替える際のピクセル数
      var pc_y = _this.countY;
      var calcx = 0;
      var calcy = 0;
      if (_this.fingerFlag === 1) {　//指の数
        _this.zoomChange(event)
      }
      else if(_this.drawFlag === 0){
          _this.drawFlag = 1

          // 距離を計算
        var disX = event.targetTouches[0].pageX
        var disY = event.targetTouches[0].pageY
        var distanceX = disX - _this.touchStartPointX;
        var distanceY = disY - _this.touchStartPointY;
        if(_this.zoomNow !== 1){
          pc_x = _this.enlargeCountX;
          pc_y = _this.enlargeCountY * 4;
        }
        _this.touchSumX = _this.touchSumX + distanceX
        _this.touchSumY = _this.touchSumY + distanceY
        _this.touchStartPointX = disX;
        _this.touchStartPointY = disY;
        if(_this.touchSumX >= 0){
          calcx = Math.floor((_this.touchSumX / pc_x))
        }
        else{
          calcx = Math.ceil((_this.touchSumX / pc_x))
        }
        if(_this.touchSumY >= 0){
          calcy = Math.floor((_this.touchSumY / pc_y))
        }
        else{
          calcy = Math.ceil((_this.touchSumY / pc_y))
        }
        _this.x += calcx
        _this.y += calcy
        _this.touchSumX %= pc_x
        _this.touchSumY %= pc_y
        if(_this.x > _this.countX - 1){
          _this.x = 0
        }
        else if(_this.x < 0){
          _this.x = _this.countX - 1
        }
        if(_this.y > _this.countY - 1){
          _this.y = 0
        }
        else if(_this.y < 0){
          _this.y = _this.countY - 1
        }
        _this.imgSrc = _this.imgDataset[_this.y][_this.x]
        _this.drawframe()
        setTimeout(function (){
          _this.drawFlag = 0
        },130)
      }
    },
    imgTouchEnd:function (event){
      this.touchStartPointX = 0;
      this.touchStartPointY = 0;
      this.touchSumX = 0;
      this.touchSumY = 0;
      if(event.targetTouches.length === 0){
        this.fingerFlag = 0;
      }
    },
    imgMouseStart(event){
      this.touchStartPointX = event.x;
      this.touchStartPointY = event.y;
      this.mouseFlag = 1
    },
    imgMouseMove(event){
      const _this = this
      if(_this.mouseFlag == 0){
        return;
      }
      if (_this.drawFlag === 0)
      {
        _this.drawFlag = 1

      var pc_x = 24;  // x方向で画像を切り替える際のピクセル数
      var pc_y = 24;
      var calcx = 0;
      var calcy = 0;
      var disX = event.x
      var disY = event.y
      var distanceX = disX - _this.touchStartPointX;
      var distanceY = disY - _this.touchStartPointY;

      if(_this.zoomNow !== 1){
        pc_x = 24;
        pc_y = 40;
      }
      _this.touchSumX = _this.touchSumX + distanceX
      _this.touchSumY = _this.touchSumY + distanceY
      _this.touchStartPointX = disX;
      _this.touchStartPointY = disY;
      if(_this.touchSumX >= 0){
        calcx = Math.floor((_this.touchSumX / pc_x))
      }
      else{
        calcx = Math.ceil((_this.touchSumX / pc_x))
      }
      if(_this.touchSumY >= 0){
        calcy = Math.floor((_this.touchSumY / pc_y))
      }
      else{
        calcy = Math.ceil((_this.touchSumY / pc_y))
      }
      _this.x += calcx
      _this.y += calcy
      _this.touchSumX %= pc_x
      _this.touchSumY %= pc_y
      if(_this.x > _this.countX - 1){
        _this.x = 0
      }
      else if(_this.x < 0){
        _this.x = _this.countX - 1
      }
      if(_this.y > _this.countY - 1){
        _this.y = 0
      }
      else if(_this.y < 0){
        _this.y = _this.countY - 1
      }
      _this.imgSrc = _this.imgDataset[_this.y][_this.x]
      _this.drawframe()
        setTimeout(function (){
          _this.drawFlag = 0
        },130)
      }
    },
    imgMouseEnd(event){
      this.touchStartPointX = 0;
      this.touchStartPointY = 0;
      this.mouseFlag = 0
    },
    imgMouseWheel(event){                     //throttle function
      const _this = this
      if(_this.wheelFlag === 1){
        _this.wheelFlag = 0
        var flag = event.deltaY > 0?1:-1
        var zoomChangeCount = _this.zoomNow + flag
        if(zoomChangeCount > 1){
          _this.isSwitch = true
          _this.changeSwitch(_this.isSwitch)
          if(zoomChangeCount > _this.zoomCount) zoomChangeCount = _this.zoomCount
        }
        else{
          _this.isSwitch = false
          _this.changeSwitch(_this.isSwitch)
          if(zoomChangeCount < 1) zoomChangeCount = 1
        }
        _this.changeSlider(zoomChangeCount)
        _this.sliderNumber = zoomChangeCount

      }
      else if ((_this.wheelFlag === 0) && (_this.wheelFlag2 === 0)){
        _this.wheelFlag2 = 1
        setTimeout(function (){
          _this.wheelFlag = 1
          _this.wheelFlag2 = 0
        },300)
      }

    },
    Wheel(event){

    },
    getData(){
      const _this = this
      _this.name = this.$route.params.name
      _this.furigana = this.$route.params.furigana
      _this.zoomCount = this.$route.params.zoomCount
      _this.addressName = this.$route.params.address
      _this.imgType = this.$route.params.defaultType
      _this.x = this.$route.params.firstimgX
      _this.y = this.$route.params.firstimgY
      _this.countX = this.$route.params.countX
      _this.countY = this.$route.params.countY
      _this.normalX = _this.countX
      _this.normalY = _this.countY
      _this.enlargeCountX = this.$route.params.enlargeCountX
      _this.enlargeCountY = this.$route.params.enlargeCountY
      if(_this.zoomCount == 1) _this.isDisable = true
      _this.extraPara = this.$route.params.extraPara
      _this.extraData = this.$route.params.extraData
      for(var i=1;i<=Object.keys(_this.extraPara).length;i++)
      {
        _this.extraParameter[i-1] = _this.extraPara[i]
        _this.extraDataset[i-1] = _this.extraData[i]
      }
    },
  },
  computed: {
  },
  watch:{
  },
  mounted() {
    const _this = this
    var firstsrc = _this.addressName + '/' + _this.imgType + '/1/data' + _this.y + '/image'+ ('000' + _this.x).slice(-3) + '.' +_this.imgType
    _this.imgSrc = require('/public/picdata/' + firstsrc)
    _this.drawframe()

  },
  created() {
    const _this = this
    _this.getData()
    _this.sizeinitialize()
    window.onresize = () => {
      return (() => {
        _this.sizeinitialize()
      })()
    }
    this.loadimg(_this.countY,_this.countX)
  },

};
</script>

<style scoped>
.position{
  text-align: center;
}
el-slider{
  hidden:true;
}

.el-divider{
  margin: 5px 0;
  background: 0 0;
  border-top: 1px solid #E6EBF5;
}
.block{

  margin: auto;
  text-align: center;
  margin-bottom: 5px;
}
.block2{
  width:35%;
  margin: auto;
  text-align: center;
  margin-bottom: 5px;
}

.image-middle{
  text-align: center;
  align-content: center;
  margin: auto;

}
canvas{

  width: var(--width);
  height: var(--height);
}
.elbutton{
  text-align: center;
  margin:auto;
}
.text{
  width: 80%;
  text-align: center;
  margin: auto;
}
.position2{
  text-align: center;
  font-size: xx-large;
}
</style>