import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import yumiya from "../views/yumiya";
import Index from "../views/Index";
import summary from "../views/summary";
import content from "../views/content";

Vue.use(VueRouter)

const routes = [　//メニューを配置される場所

  {
    path: '/',
    show: true,
    name: "紹介",
    redirect:"/introduce",
    component: Index,
    children:[
      {
        path: '/introduce',
        name: "弓矢町と鎧兜について",
        show: true,
        component: Home
      },
      {
        path: '/gaibu',
        name: "外部説明",
        show: true,
        component: yumiya
      }
    ]
  },
  {
    path: '/summary',
    name: "まとめ",
    show: true,
    component: summary
  },
  {
    path: '/content',
    show: false,
    name: "content",
    component: content
  },

]

const router = new VueRouter({
  mode: 'hash',
  // base: process.env.BASE_URL,
  routes
})

export default router
