<template>
  <el-container>
    <el-header>
      <my-header></my-header>
    </el-header>

    <el-main>
      <router-view></router-view>
    </el-main>
  </el-container>
</template>


<script>
import header from '@/components/Header'

export default {
  components: {
    'my-header': header
  },
  name: "HomeIndex1",
  data(){
    return{
    }
  },
  methods:{
    imgLoad(){
      this.$nextTick(()=>{
        if(document.documentElement.clientWidth>1024){
          this.imgWidth = 1024;
          this.imgHeight = `${this.$refs.imgH[0].height}`;
        }
        else
        {
          this.imgWidth = document.documentElement.clientWidth;
          this.imgHeight = document.documentElement.clientWidth / 1024 * 255;
        }
      })
    }
  },
  mounted () {
    this.imgLoad();
    window.addEventListener("resize",this.imgLoad,false);
  },
  destroyed(){
    window.removeEventListener("resize",this.imgLoad,false);
  },
}
</script>

