  el-carousel{
    text-align: center;
  }
  el-carousel__item h3{
    margin: 0;
    width: 100%;
  }
  img{
    align-content: center;
    height: 100%;
    width: 100%;
  }
  .text1{
    width: 80%;
    margin: auto;
  }
  .el-divider{
    margin: 8px 0;
    background: 0 0;
    border-top: 1px solid #E6EBF5;
  }
  .el-container {
    height: calc(97vh);
  }
  .el-submenu .el-menu-item{
    min-width: 30px!important;
  }

  .el-header {
    padding: 0 0px;
  }
