<template>
    <el-container>
      <el-header>
        <my-header></my-header>
      </el-header>

      <el-main>
        <div :style="note">
          <div class="position">{{name}}</div>
          <div class="images">
            <div v-if="list" v-for="(item, index) in list" :key="index" class="image-middle">
              <el-card shadow="hover" :body-style="{ padding: '10px' }">
                <el-popover>
                  <img :src="list[index].get('datasrc')" slot="reference" class="image"/>
                  <img :src="list[index].get('datasrc')" class="imagePreview" :style="{'--height': sizeVar,'--width': sizeVar}"/>
                </el-popover>
                <div style="text-align:center;padding-top:12px">
                  <el-button size="mini" round @click="jumpto(index)">{{list[index].get("name")}} </el-button>
                </div>
              </el-card>

            </div>
          </div>
        </div>
            </el-main>
          </el-container>
</template>

<script>
//<el-link v-bind:href="info[index].link">
import header from '@/components/Header'
export default {
  components: {
    'my-header': header
  },
  name: "summary",
  props:{
  },
  data() {
    return {
      note: {
        backgroundImage: "url(" + require("/public/background.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundClip: "content-box",
        width: "100%",
        position: "absolute",
        left: "0px",
      },
      name:"京都弓矢町弓箭閣鎧兜のデジタルコンテンツ",
      sizeVar:"500px",
      tableData:[],
      info:[],
      testset:[],
      list:[],
      extraPara:[],
      extraData:[],
      data:[

        {
          address: "atago",
          countx: 24,
          county: 24,
          defaulttype: "webp",
          enlargecountx: 24,
          enlargecounty: 10,
          extradata: {},
          extrapara: {},
          firstimgx: 23,
          firstimgy: 0,
          furigana: "あたごじるし",
          name: "愛宕印",
          zoomcount: 4,
        },

        ]
    };
  },
  methods: {
    jumpto:function (indexnumber){
      const _this = this
      _this.$router.push({
        name: "content",
        params: {
          name: _this.list[indexnumber].get("name"),
          furigana: _this.list[indexnumber].get("furigana"),
          zoomCount: _this.list[indexnumber].get("zoomCount"),
          address: _this.list[indexnumber].get("address"),
          defaultType: _this.list[indexnumber].get("defaultType"),
          firstimgX: _this.list[indexnumber].get("firstimgX"),
          firstimgY: _this.list[indexnumber].get("firstimgY"),
          countX: _this.list[indexnumber].get("countX"),
          countY: _this.list[indexnumber].get("countY"),
          enlargeCountX: _this.list[indexnumber].get("enlargeCountX"),
          enlargeCountY: _this.list[indexnumber].get("enlargeCountY"),
          extraPara: _this.list[indexnumber].get("extraPara"),
          extraData: _this.list[indexnumber].get("extraData"),
        },
      });
    },
    sizeinitialize(){
      const _this = this
      _this.windowHeight = document.documentElement.clientHeight; // 高
      _this.windowWidth = document.documentElement.clientWidth; // 宽
      if(_this.windowHeight - 100 > _this.windowWidth){
        var imgsize = _this.windowWidth - 30
      }
      else {
        var imgsize = _this.windowHeight - 130
      }
      if(imgsize < 500){
        _this.sizeVar = imgsize + 'px'
        _this.sizeVar = imgsize + 'px'
      }
    },
    getData(data){   //データの獲得
      const _this = this
      for(var i=0;i<data.length;i++) {
        _this.tableData[i] = {
          name: data[i].name,
          furigana: data[i].furigana,
          zoomCount: data[i].zoomcount,
          address: data[i].address,
          defaultType: data[i].defaulttype,
          firstimgX: data[i].firstimgx,
          firstimgY: data[i].firstimgy,
          countX: data[i].countx,
          countY: data[i].county,
          enlargeCountX: data[i].enlargecountx,
          enlargeCountY: data[i].enlargecounty,
          extraPara: data[i].extrapara,
          extraData: data[i].extradata,
        }
      }
    },
    setData(){
      const _this = this
      const isSupportWebp = document.createElement('canvas').toDataURL('image/webp').indexOf('data:image/webp') === 0;
      var info = []
      for(var i=0;i<_this.tableData.length;i++) {　　　　//各対象物のデータを分割する
        var myMap = new Map();
        myMap.set("name",_this.tableData[i].name)
        myMap.set("furigana",_this.tableData[i].furigana)
        myMap.set("zoomCount",_this.tableData[i].zoomCount)
        if(isSupportWebp){
          myMap.set("defaultType",_this.tableData[i].defaultType)
        }
        else{
          myMap.set("defaultType","jpg")
        }
        myMap.set("firstimgX",_this.tableData[i].firstimgX)
        myMap.set("firstimgY",_this.tableData[i].firstimgY)
        myMap.set("countX",_this.tableData[i].countX)
        myMap.set("countY",_this.tableData[i].countY)
        myMap.set("enlargeCountX",_this.tableData[i].enlargeCountX)
        myMap.set("enlargeCountY",_this.tableData[i].enlargeCountY)
        myMap.set("address",_this.tableData[i].address)
        var src = _this.tableData[i].address + '/' + myMap.get("defaultType") +  '/1/data'+ _this.tableData[i].firstimgY + '/image'+ ('000' + _this.tableData[i].firstimgX).slice(-3) + '.' + myMap.get("defaultType");
        myMap.set("datasrc",require("/public/picdata/" + src))
        var extraPara = JSON.stringify(_this.tableData[i].extraPara)
        myMap.set("extraPara",JSON.parse(extraPara))
        var extraData = JSON.stringify(_this.tableData[i].extraData)
        myMap.set("extraData",JSON.parse(extraData))
        info.push(myMap)
      }
      _this.list = info
    },
  },
  created() {
    const _this = this
    _this.sizeinitialize()
    // Promise.all([
    //   new Promise((resolve) =>{
    //     axios.get('http://192.168.3.28:8181/imagedata/findAll/').then(function (resp) {//リクエスト送信
    //       _this.getData(resp)
    //       resolve("done")
    //     })
    //   })
    // ]).then(res => {
    //   _this.setData()
    // })
    _this.getData(_this.data)
    _this.setData()
    window.onresize = () => {
      return (() => {
        _this.sizeinitialize()
      })()
    }
  },
  mounted() {
  }
};

</script>

<style scoped>

.position {
  /*margin-left: 15px;*/
  text-align: center;
  font-size: 30px;
  font-weight: 600;
}
/* 画像全体のレイアウト */
.images{
  display: flex;
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  flex-wrap: wrap;
}
/* 画像間で */
.image-middle{
  margin-right: 15px;
  margin-bottom: 15px;
}
/* 一つの画像に対するレイアウト */
.image{
  width:250px;
  height:250px;
}
.imagePreview{
  width: var(--width);
  height: var(--height);
}
</style>